}
@font-face {
  font-family: 'g-regular';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.background-blur {
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.backdrop-shadow {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.0),
                    0 22px 70px 4px rgba(0,0,0,0.55),
                    0 0 0 1px rgba(0, 0, 0, 0.0);
}

.selectedBtn {
  background-color: var(--text1) !important;
  border: 1px solid var(--text2) !important;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
  color: var(--text);
  background-color: var(--background);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#sidebar {
  position: absolute;
  top: 0;
  left: 0;
  width: 196px;
  height: 100vh;

  background-color: var(--backgroundTran);
  border-right: 2px solid var(--text2);

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 36px;
}

#sidebar .sidebarAction {
  margin-top: 18px;
  width: calc(100% - 36px);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

#sidebar .sidebarAction div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

#profileImg {
  width: 30px;
  height: 30px;
  border: 1px solid rgba(242, 242, 242, 0.36);
  border-radius: 30px;
}
#profileName {
  font-family: 'g-regular';
  font-size: 16px;
}

#sidebarCloseBtn {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
#sidebarCloseBtn img {
  width: 28px;
  height: 28px;
  margin: 0;
  padding: 0;
}

#sidebar .taskbarAction {
  width: calc(100% - 36px);
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start; 
  gap: 12px;
}

#sidebar .taskbarAction button {
  background-color: var(--backgroundTran);
  border: 1px solid var(--text1);
  border-radius: 4px;
  padding: 8px 16px;
  width: 160px;
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}
#sidebar .taskbarAction button:hover {
  background-color: var(--text1);
  border: 1px solid var(--text2);
}
#sidebar .taskbarAction button:active{
  background-color: var(--backgroundTran);
  border: 1px solid var(--text1);
}

.sidebarIcon svg {
  margin-bottom: -3px;
  height: 18px;
  width: 18px;
}

.sidebarText {
  color: var(--text);
  font-family: 'g-regular';
  font-size: 14px;
  text-align: center;
}

#window {
  position: absolute;
  top: 0;
  right: 0;
  width: calc(100% - 196px);
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
}

.sectionContainer {
  position: absolute;
  top: 64px;
  left: 64px;
  width: calc(100% - 128px);
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  gap: 16px;
}

.sectionHeader {
  font-family: 'g-regular';
  font-size: 2rem;
}

.sectionTask {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.taskContainer {
  display: flex;
  flex-direction: row;
  border: 1px solid var(--text1);
  border-radius: 4px;
  padding: 8px 16px;
  gap: 12px;
}
.taskActions {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px;
}
.taskDoneBtn, .taskEditBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: var(--backgroundTran);
  border: 1px solid var(--text1);
  border-radius: 4px;
  padding: 8px;
}
.taskDoneBtn:hover {
  background-color: var(--text1);
  border: 1px solid var(--text2);
}
.taskEditBtn:hover {
  background-color: var(--text1);
  border: 1px solid var(--text2);
}
.taskDoneBtn:active {
  background-color: var(--backgroundTran);
  border: 1px solid var(--text1);
}
.taskEditBtn:active{
  background-color: var(--backgroundTran);
  border: 1px solid var(--text1);
}
.taskDoneBtn svg, .taskEditBtn svg {
  width: 14px;
  height: 14px;
}
.taskDetails {
  display: flex;
  flex-direction: column;
  gap: 2px;
  padding: 8px 0;
}

.taskName {
  font-family: 'g-light';
  font-size: 1rem;
}

.taskDesc {
  font-family: 'g-light';
  font-size: 0.8rem;
}

.taskDate {
  font-family: 'g-light';
  font-size: 0.8rem;
  color: var(--accent);
}

.taskLabel {
  font-family: 'g-light';
  font-size: 1rem;
}

#taskFormContainer {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 32px;
}
#taskForm {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px;
}
#taskForm input {
  font-size: 0.8rem;
  text-align: center;
  width: 200px;
  padding: 8px 16px;
  color: var(--text);
  background-color: var(--backgroundTran);
  border: 1px solid var(--text1);
  border-radius: 4px;
}
#taskForm input::placeholder {
  color: var(--text);
  font-family: 'g-regular';
}
#taskForm input[type='date'] {
  font-size: 0.8rem;
  font-family: 'g-regular';
  background-color: var(--textTran);
  color: var(--background);
}
#taskForm textarea {
  text-align: center;
  width: 200px;
  padding: 8px 16px;
  color: var(--text);
  background-color: var(--backgroundTran);
  border: 1px solid var(--text1);
  border-radius: 4px;
}
#taskForm textarea::placeholder {
  color: var(--text);
  font-family: 'g-regular';
}
#taskForm select {
  font-size: 0.8rem;
  font-family: 'g-regular';
  text-align: center;
  width: 234px;
  padding: 8px 16px;
  border-radius: 4px;
  color: var(--text);
  border: 1px solid var(--text1);
  background-color: var(--backgroundTran);
}
#taskForm button[type='submit'] {
  font-size: 0.8rem;
  font-family: 'g-regular';
  text-align: center;
  width: 234px;
  padding: 8px 16px;
  border-radius: 4px;
  border: 1px solid var(--text1);
  color: var(--background);
  background-color: var(--textTran);
  cursor: pointer;
}
#taskFormClose {
  position: absolute;
  top: 32px;
  right: 32px;
  color: var(--text);
  background-color: var(--backgroundTran);
  border: 1px solid var(--text1);
  border-radius: 4px;
  padding: 8px 16px;
  text-align: center;
  cursor: pointer;
